@import url(https://fonts.googleapis.com/css?family=Arimo|Barlow|Heebo|Karla|Muli|Quicksand|Raleway&display=swap);
html,
body,
div#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #1e1e1e;
}


.App {
  width: 100%;
  height: 100%;
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-size: 0.9em;
  color: rgba(255, 255, 255, 0.45);
  background: rgba(5, 5, 5, 0.7);
}
.APmenu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
.APoffset {
  height: 47px;
}
.APhqSelector {
  width: 100%;
  height: 100%;
}
.APtimelineContainer {
  overflow-y: scroll;
  height: 90%;
  padding-bottom: 100px;
}

.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background: rgba(255, 255, 255, 0.05);
}
.MNmenu {
  width: 100vw;
}
.ant-menu-dark {
  background: #0a0a0a;
}
.ant-menu-dark .ant-menu-sub {
  background: #0a0a0a;
}
.ant-menu-item {
  background: #0a0a0a;
}
@media only screen and (max-width: 800px) {
  .ant-menu-dark {
    background: transparent;
  }
  .ant-menu-dark .ant-menu-sub {
    background: #0a0a0a;
  }
  .ant-menu-item {
    background: #0a0a0a;
  }
}

.UIcontrols {
  flex-wrap: nowrap;
  flex-shrink: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
  max-height: 350px;
  min-height: 190px;
  max-width: 550px;
}
.UIioTitle {
  display: block;
  color: rgba(255, 255, 255, 0.6);
}
.UIioTitlePadded {
  display: block;
  padding-top: 5px;
  color: rgba(255, 255, 255, 0.6);
}
.UIpinBox {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 80px;
  height: 100%;
}
.UIpinUnit {
  padding: 0px 0px 0px 0px;
  min-width: 80px;
  vertical-align: middle;
}
.UIvSliderUnit {
  height: calc(100% + 5px - 20px);
  min-height: 190px;
  width: 40px;
  text-align: center;
}
.UIvSliderTitle {
  position: relative;
  top: -8px;
}
.UIvSliderBox {
  text-align: center;
  height: 100%;
}
.UIvSliderBoxSub {
  display: flex;
  flex-wrap: nowrap;
  height: 100%;
  min-width: 90px;
  padding-top: 3px;
}
.UIdialBox {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  text-align: center;
}
.UIcaptureIconUnit {
  display: block;
  margin: auto;
  font-size: 1.5em;
  color: #6c98d0;
}
.UIdial {
  position: relative;
  top: -5px;
}
.UImotorId {
  display: block;
  color: rgba(255, 255, 255, 0.65);
  font-size: 1.75em;
  position: relative;
  top: 2px;
}
@media only screen and (max-width: 600px) {
  .UIcontrols {
    padding: 0px 20px 0px 20px;
    max-height: 250px;
  }
  .UIcaptureIconUnit {
    font-size: 1.25em;
  }
  .UImotorId {
    font-size: 1.5em;
  }
  .UIvSliderUnit {
    width: 30px;
  }
  .UIdialBox {
    position: relative;
    left: 0px;
  }
}

