@import '~antd/dist/antd.css';
.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background: rgba(255, 255, 255, 0.05);
}
.MNmenu {
  width: 100vw;
}
.ant-menu-dark {
  background: #0a0a0a;
}
.ant-menu-dark .ant-menu-sub {
  background: #0a0a0a;
}
.ant-menu-item {
  background: #0a0a0a;
}
@media only screen and (max-width: 800px) {
  .ant-menu-dark {
    background: transparent;
  }
  .ant-menu-dark .ant-menu-sub {
    background: #0a0a0a;
  }
  .ant-menu-item {
    background: #0a0a0a;
  }
}
