.App {
    width: 100%;
    height: 100%;
	font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
	font-size: .9em;
	color: @midGray;  
	background: fade(@bgAll, 70%);
}

.APmenu {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 100;
}

.APoffset {
	height: 47px;
}

.APhqSelector {
    width: 100%;
    height: 100%;	
}

.APtimelineContainer {
	overflow-y: scroll;
	height: 90%;
	padding-bottom: 100px;
}
