@timelineHeight   : 40vh;
@timelineBoxHeight: calc(40vh + 50px);
@borderColor      : rgb(20, 20, 20);

.HQcontainer {
  width          : 100%;
  height         : 100%;
  display        : flex;
  justify-content: flex-start;
  flex-direction : column;
}

.HQtopContainer {
  height          : calc(100% - @timelineBoxHeight);
  width           : 100%;
  display         : flex;
  flex-wrap       : nowrap;
  flex-shrink     : 0;
  align-items     : center;
  justify-content : space-evenly;
  border          : 5px solid @borderColor;
}

.HQconsole {
  width       : 100%;
  max-width   : calc(100vw - 400px);
  min-width   : 400px;
  height      : 100%;
  border-right: 5px solid @borderColor;
}

.HQuiControls {
  width          : 40%;
  max-width      : 800px;
  min-width      : 400px;
  height         : 100%;
  padding        : 10px 0px 10px 10px;
  border-radius  : 5px;
  display        : flex;
  flex-wrap      : nowrap;
  flex-shrink    : 0;
  justify-content: center;
  align-items    : center;
}

.HQtimelineBox {
  width          : 100%;
  padding        : 5px 20px 10px 5px;
  display        : flex;
  flex-wrap      : nowrap;
  flex-direction : column;
  flex-shrink    : 0;
  justify-content: center;
  height         : @timelineHeight;
  position       : absolute;
  bottom         : 0px;
}

.HQtimelineButtonRow {
  padding  : 5px 20px 5px 55px;
  flex-wrap: nowrap;
}

.HQtimelineButtonUnit {
  padding            : 0px 12px 0px 12px;
}

.HQbuttonText {
  color    : @midGray;
  font-size: 1.2em;
}


@media only screen and (max-width: 800px) {
  .HQcontainer {
    position        : static;
    justify-content : flex-start
  }

  .HQtopContainer {
    justify-content : center;
    border          : 0px;
  }

  .HQuiControls {
    padding         : 5px 0px 18px 10px;
    width           : 100vw;
    border-radius   : 0px;
  }

  .HQtimelineBox {
    border          : 0px;
  }

  .HQtimelineButtonRow {
    padding: 0px 5px 5px 10px;
  }

  .HQtimelineButtonUnit {
    padding: 0px 0px 0px 10px;
  }

  .HQbuttonText {
    color    : @hiGray;
    font-size: 1em;
  }

}

@media only screen and (max-height: 500px) {
  .HQtimelineBox {
    height          : calc(100% - 50px);
    border-top      : 0px;
  }
}
